body {
  margin: 0; }

.root {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif; }

a {
  color: gray; }

h2, h3 {
  line-height: 1.8;
  margin: 5px 0; }

p, li {
  line-height: 1.5; }

hr {
  color: #f3df49;
  background-color: #f3df49;
  height: 3px;
  border: none; }

@media (max-width: 800px) {
  .root {
    font-size: 0.8em; } }
