.main {
  .main-section {
    padding: 20px; }
  .common {
    display: flex;
    max-width: 1000px;
    margin: 0 auto;
    .common-item {
      flex: 1 1 auto;
      margin: 10px;
      h2 {
        border-bottom: 3px solid #f3df49; }
      p {
        line-height: 1.5; } } }
  .reasons {
    text-align: center;
    .reasons-list {
      display: flex;
      max-width: 80%;
      margin: 0 auto;
      .reason {
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: center;
        min-width: 200px;
        padding: 10px;
        margin: 10px;
        text-align: justify;
        .picture {
          width: 125px;
          fill: #2d3132; }
        .reason-text {
          max-width: 100%;
          margin-top: 0; } } } }
  .register-form {
    padding-top: 50px;
    padding-bottom: 50px;
    display: none;
    max-width: 70%;
    margin: auto;
    .tech-table-wrapper {
      border: 1px solid lightblue;
      margin-top: 10px;
      padding: 5px; }
    .tech-table {
      display: table;
      margin-left: auto;
      margin-right: auto;
      margin-top: 10px;
      margin-bottom: 20px;
      .tech {
        display: table-row;
        &:hover {
          background: lightgoldenrodyellow; }
        .tech-name {
          display: table-cell;
          max-width: 200px; }
        label {
          display: table-cell;
          text-align: center;
          cursor: pointer;
          &:hover {
            background: lightblue; } } }
      .tech-header {
        display: table-header-group;
        label {
          display: table-cell;
          padding: 5px 7px; } } } }
  .place {
    padding-top: 55px; }
  .place-wrapper {
    display: flex;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    .place-description {
      padding-right: 20px;
      padding-bottom: 20px; } }
  .inventos-photo {
    width: 100%; }
  .place-description {
    flex: 1 1 auto; }
  .yandex-map {
    flex: 1.5 1 800px;
    // margin: auto;
    padding: 25px 0;
    iframe {
      width: 560px;
      height: 100%;
      min-height: 350px;
      max-width: 100%; } }
  .contacts {
    margin: auto;
    text-align: center;
    .contact-form {
      display: none; } } }

@media (max-width: 800px) {
  .main {
    .common {
      flex-wrap: wrap; }
    .reasons .reasons-list {
      display: flex;
      flex-wrap: wrap;
      max-width: none; }
    .register-form {
      max-width: 100%; }
    .place-wrapper {
      max-width: 100%;
      flex-wrap: wrap; } } }
