.main-header .back {
  width: 100%; }

.nav-menu {
  display: flex; }

.nav-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  margin: 5px 0;
  ul {
    margin: 0; }
  .vk {
    display: inline-block;
    width: 22px;
    height: 18px;
    background: url('data:image/svg+xml;base64,') no-repeat;
    background-size: 22px; }
  .name-and-logo {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: inherit;
    .logo {
      width: 50px;
      margin-right: 10px;
      flex: 0 0 auto;
      height: 50px;
      background: url('../img/orel-JS-01.svg');
      background-size: 50px; }
    .name {
      font-size: 1.6em;
      flex: 0 1 auto;
      &.meetup {
        padding-right: 10px; } } }
  nav li {
    display: inline-block;
    list-style-type: none;
    padding: 7px;
    font-size: 1.2em;
    a {
      text-decoration: none;
      color: black;
      &:hover {
        text-decoration: underline; } } } }

.image-block {
  position: relative;
  overflow: hidden;
  .description {
    position: absolute;
    background-color: rgba(120, 112, 106, 0.6);
    margin: 20px;
    top: 20px;
    color: rgb(243, 223, 73);
    max-width: 600px;
    font-family: Georgia;
    font-style: italic; }
  .back {
    min-width: 650px; }
  .description {
    .description-main {
      font-size: 1.5em;
      letter-spacing: 1px;
      padding: 15px; }
    .description-extra {
      font-size: 1.2em;
      padding: 15px; } } }

@media (max-width: 800px) {
  .nav-block {
    justify-content: center;
    nav {
      display: none; } } }

@media (max-width: 400px) {
  .image-block {
    .back {
      min-width: 200px; }
    .description {
      font-size: 0.8em;
      margin: 0;
      top: 0;
      .description-main, .description-extra {
        padding: 5px; } } } }
